import { Datos_personales } from "./componentes/datos_personales";
/**
 * funcion Home
 * lo importante es saber que desde "data" , se sustraen todos los datos que vamos
 * a tilizar, los cuales vienen en Arrays y object
 */
export default function Home(data) {
  /**
   *  lo primero que vamos hacer es crear un componente "jsx", para lograr dise√±ar un portafolio
   * en el que podamos ingresar la informacion de manera dinamica, por eso "HV" es un compenente Reactivo
   * el cual va representar toda la informacion que nos traiga "data",
   */
  const HV = (
    <>
      <div className={`wrapper cf`}>
        <div className={`content`}>
          {`${Datos_personales(data.datos_personales, data.iconos)}`}
          <br />
          <div className={`idiomas`}>
            <h3>IDIOMAS</h3>
            <div className={`contacto_info`}></div>
          </div>
        </div>

        <div className={`sidebar`}>
          <h3>EXPERINECIA LABORAL</h3>
        </div>
        <br />

        <div></div>
        <div className={`sidebar`}>
          <h3>ECUCACION </h3>
        </div>
        <br />
        <div className={`sidebar`}>
          <h3>ACTITUDES</h3>
        </div>
        <br />
        <div className={`sidebar`}>
          <h3>PROYECTOS</h3>
        </div>
      </div>
    </>
  );
  return HV;
}
