export const Datos_personales = (data, iconos) => {
  const datos_personales = (
    <div>
      <img className={`imag_perfil`} src={data.foto_perfil} alt="imagenes" />

      <div className={`tarjeta_presentacion`}>
        <br />
        <div className={`tarjeta_presentacion_info`}>
          <div id="left">
            <a
              href={`https://www.canva.com/design/DAFD6jQ4IUQ/tX_-2mVUAqanU4t82OXJbQ/edit?utm_content=DAFD6jQ4IUQ&utm_campaign=designshare&utm_medium=link2&utm_source=sharebutton`}
            >
              <img src={iconos.dowload} alt="dowload" />
            </a>

            <h1>{data.nombre}</h1>
          </div>
        </div>
        <div className={`idiomas`}>
          <h3>CONTACTO</h3>
        </div>
        <div className={`contacto_info`}>
          <p className={`contacto_detalle`}>
            <img src={iconos.email} alt="email" />
            {`    ${data.correo}`}
          </p>
          <p className={`contacto_detalle`}>
            <img src={iconos.telefono} alt="email" />
            {`    ${data.celular}`}
          </p>
          <p className={`contacto_detalle`}>
            <img src={iconos.direccion} alt="email" />
            {`    ${data.direccion}`}
          </p>
        </div>
      </div>
    </div>
  );
  return datos_personales;
};
